.border-secondary {
  border-color: darken($secondary-500, 20%) !important;
}

.text-secondary {
  color: darken($secondary-900, 5%) !important;
}

.breadcrumb {
  background-color: desaturate(lighten($primary-500, 65%), 25%);
}

.dropdown-item.active {
  background-color: lighten($primary-500, 60%);
}

.nav-pills .nav-link.active,
.nav-pills .show > .nav-link {
  color: #fff;
  background-color: lighten($primary-500, 20%);
}

.table-hover tbody tr:hover {
  background-color: lighten($primary-500, 65%);
}
